<template>
	<view class="box">
		<scroll-view class="list" scroll-y @scrolltolower="handleLower">
			<address-detail v-for="(item, index) in adderssList" class="pull-order-item-out" :key="item.ID"
				:order="item" @updataDefault="handleUpdataDefault" @delAddress="handleDelAddress">
			</address-detail>
			<view class="nono-list">
				{{lower?'':`————&nbsp&nbsp&nbsp&nbsp已加载完成&nbsp&nbsp&nbsp&nbsp————`}}
			</view>
		</scroll-view>
		<view class="btn" @click="addAddress">新增地址</view>
	</view>
</template>

<script>
	import {
		GetListByPageAjax,
		GetSetDefaultAjax,
		GetDelAddressAjax
	} from '@/apis/api/modules/user-center'
	// import ListPage from '@/frame/utils/create-list'
	import AddressDetail from './components/address-detail/index.vue'
	// const list = new ListPage({
	// 	api: GetListByPageAjax,
	// 	data: {
	// 		token: true
	// 	}
	// })
	export default {
		// mixins: [list],
		components: {
			AddressDetail
		},
		data() {
			return {
				adderssList: [],
				pageIndex: 1,
				lower: true
			}
		},
		onShow() {
			this.getData()
			uni.removeStorageSync('addressList')
		},
		// onLoad() {
		// 	// this.getPageList()
		// 	this.getData()
		// },
		methods: {
			async handleLower() {
				// if (!this.lower) {
				// 	return false
				// }
				this.pageIndex = this.pageIndex + 1
				const result = await GetListByPageAjax({
					pageIndex: this.pageIndex,
					token: true
				})
				if (result.Result.Data.length > 0) {
					this.lower = true
					this.adderssList.push(...result.Result.Data)
					return false
				}
				this.lower = false
			},
			async handleUpdataDefault(e) {
				await GetSetDefaultAjax({
					id: e,
					token: true
				})
				uni.showToast({
					title: '设置默认地址成功',
					icon: 'success'
				})
				this.getData()
			},
			async handleDelAddress(e) {
				await GetDelAddressAjax({
					id: e,
					token: true
				})
				uni.showToast({
					title: '成功删除地址',
					icon: 'success'
				})
				this.getData()
			},
			async getData() {
				const result = await GetListByPageAjax({
					pageIndex: this.pageIndex,
					token: true
				})
				this.adderssList = result.Result.Data
			},
			addAddress() {
				uni.navigateTo({
					url: '/pages/user-center/user-address/address-information/index'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		background: #F7F7F7;
		padding: 30rpx;
		display: flex;
		flex-direction: column;
		height: 100%;
		box-sizing: border-box;
	}

	.list {
		flex: 1;
		overflow: auto;

		.nono-list {
			text-align: center;
			color: #c1c1ba;
			padding: 30rpx 0;
		}
	}

	.btn {
		// position: absolute;
		// bottom: 20rpx;
		width: 690rpx;
		height: 88rpx;
		background: #00D08F;
		border-radius: 44rpx;
		text-align: center;
		line-height: 88rpx;
		font-size: 28rpx;
		font-weight: 500;
		color: #FFFFFF;
	}
</style>
